import React, { useEffect, useState } from 'react';
import { Form, Input, InputNumber, Button, Select } from 'antd';
import moment from "moment";
const { Option } = Select;
const layout = {
  labelCol: {
    span: 10,
  },
  wrapperCol: {
    span: 16,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};

class StrategyUseForm extends React.Component {
  formRef = React.createRef();
  componentDidMount = () => {
    console.log({formOption: this.props.formOption})
    const { name, strategy_id, take_profit } = this.props.formOption;
    this.formRef.current.setFieldsValue({
      strategy_name: name,
      strategy_id,
      apikey: "your_api_key"+ moment().format('YYYYMMDDHHmmss'),
      secretkey: "your_secret_key"+ moment().format('YYYYMMDDHHmmss'),
      passphrase: "your_passphrase"+ moment().format('YYYYMMDDHHmmss'),
      exchange: 'binance',
      take_profit,
      initial_amount: 10001,
      });
  }

  onExchangeChange = (value) => {
    switch (value) {
		case 'binance':
		  this.formRef.current.setFieldsValue({
        strategy_name: 'bn' + moment().format('YYYYMMDDHHmmss'),
		  });
		  return;
		case 'okex':
		  this.formRef.current.setFieldsValue({
        strategy_name: 'ok' + moment().format('YYYYMMDDHHmmss'),
		  });
		  return;
		case 'huobi':
			this.formRef.current.setFieldsValue({
			  strategy_name: 'hb' + moment().format('YYYYMMDDHHmmss'),
			});
		case 'other':
			this.formRef.current.setFieldsValue({
				strategy_name: 'ot' + moment().format('YYYYMMDDHHmmss'),
			});
	  }
	};
  onFinish = (values) => {
    console.log(values);
	  this.props.new_instance(values);
  };
  onReset = () => {
    this.formRef.current.resetFields();
  };
  onFill = () => {
    this.formRef.current.setFieldsValue({
		strategy_name: 'bn' + moment().format('YYYYMMDDHHmmss'),
		strategy_id: 1,
		apikey: "your_api_key"+ moment().format('YYYYMMDDHHmmss'),
		secretkey: "your_secret_key"+ moment().format('YYYYMMDDHHmmss'),
		passphrase: "your_passphrase"+ moment().format('YYYYMMDDHHmmss'),
		exchange: 'binance',
		take_profit: 2000,
		initial_amount: 10001,
    });
  };

  render() {
    return (
      <Form {...layout} ref={this.formRef} name="control-ref" onFinish={this.onFinish}>
        <Form.Item
        name="strategy_name"
        label="Name"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input />
      </Form.Item>
        <Form.Item
        name="strategy_id"
        label="Strategy Id"
        rules={[
          {
            required: true,
          },
        ]}
      >
        {/* <Input /> */}
        <InputNumber />
      </Form.Item>
      <Form.Item
        name="take_profit"
        label="TakeProfit"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <InputNumber />
      </Form.Item>
      <Form.Item
        name="initial_amount"
        label="InitialAmount"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <InputNumber />
      </Form.Item>
      <Form.Item
        name="exchange"
        label="Exchange"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Select
          placeholder="Select a option and change input text above"
          onChange={this.onExchangeChange}
          allowClear
        >
          <Option value="okex">okex</Option>
          <Option value="huobi">huobi</Option>
          <Option value="binance">binance</Option>
        </Select>
      </Form.Item>

        <Form.Item
        name="apikey"
        label="ApiKey"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input />
      </Form.Item>
        <Form.Item
        name="secretkey"
        label="SecretKey"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input />
      </Form.Item>
      
      <Form.Item
        noStyle
        shouldUpdate={(prevValues, currentValues) => prevValues.exchange !== currentValues.exchange}
      >
        {({ getFieldValue }) =>
          getFieldValue('exchange') === 'okex' ? (
            <Form.Item
              name="passphrase"
              label="passphrase"
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Input />
            </Form.Item>
          ) : null
        }
      </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
          <Button htmlType="button" onClick={this.onReset}>
            Reset
          </Button>
          <Button type="link" htmlType="button" onClick={this.onFill}>
            Fill form
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

export default StrategyUseForm;