import React, { useEffect, useState } from 'react'
import { Form, Input, InputNumber, Table, Button, Space } from 'antd';

import { formatAllStrategies, formatAllInstances } from '../common/js/formatHelper';
import { Form_StrategistConfirmed, Form_ArbitrateUserWin, Form_ArbitrateStrategistWin, Form_Settled } from '../common/js/instanceStatusHandleForm';

const { Column } = Table;

export default function Auditor(props) {
    const { quantContract, connected, account } = props;
    const c = quantContract;
    const [mainLoading, setMainLoading] = useState(true);
    const [allInstances, setAllInstances] = useState(null);
    const [splitInstance, setSplitInstance] = useState(false);

    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, [quantContract]);

    const loadData = async () => {
        const _allInstances = await c.methods.getAllInstances().call();
        console.log("loadData _allInstances: ", _allInstances);
        setAllInstances(formatAllInstances(_allInstances));
    }

    // 
    const loadDataAuto = async () => {
        loadData();
    }
    setInterval(() => {
        console.log("Date: ", Date())
        if (quantContract) {
            loadDataAuto();
        }
    }, 300000);

    // start strategy handle ---------------------------------------------------------------------


    // start instance handle ---------------------------------------------------------------------

    // _instance_status == 0 set_instance_status_UserPreConfirmed(instance_id);
    // _instance_status == 1 set_instance_status_StrategistPreConfirmed(instance_id);     
    // _instance_status == 2 set_instance_status_UserMidDisagree(instance_id);     
    // _instance_status == 3 set_instance_status_StrategistMidDisagree(instance_id);     
    // _instance_status == 4 set_instance_status_StrategistConfirmed(instance_id);     
    // _instance_status == 5 set_instance_status_UserConfirmed(instance_id);     
    // _instance_status == 6 set_instance_status_ArbitrateUserWin(instance_id);     
    // _instance_status == 7 set_instance_status_ArbitrateStrategistWin(instance_id);     
    // _instance_status == 8 set_instance_status_Unsettled(instance_id);     
    // _instance_status == 9 set_instance_status_Settled(instance_id);

    const set_instance_status_ArbitrateUserWin = async (obj) => {
        // const { final_amount } = obj;
        const final_amount = parseInt(obj.final_amount);
        console.log("set_instance_status_ArbitrateUserWin final_amount: ", final_amount);
        const instance_id = parseInt(obj.instance_id);
        console.log("set_instance_status_ArbitrateUserWin instance_id: ", instance_id);
        await c.methods.set_instance_status_x_id_status(instance_id, 6, final_amount)
            .send({ from: account })
            .on('confirmation', (confirmationNumber, receipt) => {
                loadData();
            })
            .on('error', function (error, receipt) {
                loadData();
            });
    }
    const set_instance_status_ArbitrateStrategistWin = async (obj) => {
        // const { final_amount } = obj;
        const final_amount = parseInt(obj.final_amount);
        console.log("set_instance_status_ArbitrateStrategistWin final_amount: ", final_amount);
        const instance_id = parseInt(obj.instance_id);
        console.log("set_instance_status_ArbitrateStrategistWin instance_id: ", instance_id);
        await c.methods.set_instance_status_x_id_status(instance_id, 7, final_amount)
            .send({ from: account })
            .on('confirmation', (confirmationNumber, receipt) => {
                loadData();
            })
            .on('error', function (error, receipt) {
                loadData();
            });
    }
    const set_instance_status_Unsettled = async (obj) => {
        const { instance_id, final_amount } = obj;
        console.log("set_instance_status_Unsettled instance_id: ", instance_id);
        await c.methods.set_instance_status_x_id_status(instance_id, 8, final_amount)
            .send({ from: account })
            .on('confirmation', (confirmationNumber, receipt) => {
                loadData();
            })
            .on('error', function (error, receipt) {
                loadData();
            });
    }
    const set_instance_status_Settled = async (obj) => {
        // const { final_amount } = obj;
        const final_amount = parseInt(obj.final_amount);
        console.log("set_instance_status_Settled final_amount: ", final_amount);
        const instance_id = parseInt(obj.instance_id);
        console.log("set_instance_status_Settled instance_id: ", instance_id);
        await c.methods.set_instance_status_x_id_status(instance_id, 9, final_amount)
            .send({ from: account })
            .on('confirmation', (confirmationNumber, receipt) => {
                loadData();
            })
            .on('error', function (error, receipt) {
                loadData();
            });
    }

    // helper  -------------------------------------------------------------------
    // const changeInstanceItemExpand_ArbitrateUserWin = (index) => {
    const changeExpand_ArbitrateUserWin = (index) => {
        // allInstances.splice(index, 1, {...allInstances[index], isExpand_ArbitrateUserWin: !allInstances[index].isExpand_ArbitrateUserWin, id: allInstances[index].name + Date.now()})
        allInstances.splice(index, 1, { ...allInstances[index], isExpand_ArbitrateUserWin: !allInstances[index].isExpand_ArbitrateUserWin })
        // console.log({'i': allInstances[index]})
        console.log({ 'i item.isExpand_ArbitrateUserWin: ': allInstances[index].isExpand_ArbitrateUserWin })
        setMainLoading(!mainLoading);
    }
    const changeExpand_ArbitrateStrategistWin = (index) => {
        allInstances.splice(index, 1, { ...allInstances[index], isExpand_ArbitrateStrategistWin: !allInstances[index].isExpand_ArbitrateStrategistWin })
        console.log({ 'i item.isExpand_ArbitrateStrategistWin: ': allInstances[index].isExpand_ArbitrateStrategistWin })
        setMainLoading(!mainLoading);
    }
    const changeExpand_Settled = (index) => {
        allInstances.splice(index, 1, { ...allInstances[index], isExpand_Settled: !allInstances[index].isExpand_Settled })
        console.log({ 'i item.isExpand_Settled: ': allInstances[index].isExpand_Settled })
        setMainLoading(!mainLoading);
    }

    const buttonClassName = "btn btn-glow custom-Big-Button-admin px-3 py-2 text-light";
    const buttonColClassName = "col-xl-2 col-md-3 col-4";
    return (
        <div>
            <div className="row mt-4">
                {!connected &&
                    <div className="d-flex justify-content-center align-items-center ">
                        <h3></h3>
                    </div>
                }

                {connected && !splitInstance && 
                    <div className="d-flex justify-content-center align-items-center ">
                        {/* <p>All Unsettled Instances</p> */}
                        <h3></h3>
                    </div>
                }
                {connected && splitInstance && 
                    <div className="d-flex justify-content-center align-items-center ">
                        {/* <p>All Unsettled Instances</p> */}
                        <h3></h3>
                    </div>
                }

                {allInstances && allInstances.map((item, index) => {
                    return (
                        <div>
                            <div key={item.id}>
                                <div className="d-flex justify-content-center align-items-center margin-top:10px margin-rignt:10px ">
                                    <Table dataSource={[item]} pagination={{ position: ['none', 'none'] }}>
                                        <Column title="instance_id" dataIndex="instance_id" key="instance_id" />
                                        <Column title="strategy_id" dataIndex="strategy_id" key="strategy_id" />
                                        <Column title="name" dataIndex="name" key="name" />
                                        <Column title="sender" dataIndex="sender" key="sender" width={160} ellipsis={true} />
                                        <Column title="strategy_startup_amount" dataIndex="strategy_startup_amount" key="strategy_startup_amount" />
                                        <Column title="take_profit" dataIndex="take_profit" key="take_profit" />
                                        <Column title="stop_loss" dataIndex="stop_loss" key="stop_loss" />
                                        <Column title="tp_rate" dataIndex="tp_rate" key="tp_rate" />
                                        <Column title="exchange" dataIndex="exchange" key="exchange" />
                                        <Column title="status" dataIndex="statusText" key="status" />
                                        <Column title="user_address" dataIndex="user_address" key="user_address" />
                                        <Column title="apikey" dataIndex="apikey" key="apikey" />
                                        <Column title="secretkey" dataIndex="secretkey" key="secretkey" />
                                        <Column title="passphrase" dataIndex="passphrase" key="passphrase" />
                                        <Column title="instance_status" dataIndex="instance_status_text" key="instance_status" />
                                        {/* <p>{item.passphrase || "your_passphrase"}</p> */}
                                        <Column
                                            title="ArbitrateUserWin"
                                            key="arbitrateUserWin"
                                            render={(text, record) => (
                                                <Space size="middle">
                                                    {/* <button onClick={() => set_instance_status_ArbitrateUserWin(item.instance_id) }>User</button> */}
                                                    <button onClick={() => changeExpand_ArbitrateUserWin(index)}>User</button>
                                                </Space>
                                            )}
                                        />
                                        <Column
                                            title="ArbitrateStrategistWin"
                                            key="arbitrateStrategistWin"
                                            render={(text, record) => (
                                                <Space size="middle">
                                                    {/* <button onClick={() => set_instance_status_ArbitrateStrategistWin(item.instance_id) }>Strategist</button> */}
                                                    <button onClick={() => changeExpand_ArbitrateStrategistWin(index)}>Strategist</button>
                                                </Space>
                                            )}
                                        />
                                        {/* <Column
                                            title="Unsettled"
                                            key="Unsettled"
                                            render={(text, record) => (
                                                <Space size="middle">
                                                <button onClick={() => set_instance_status_Unsettled(item.instance_id) }></button>
                                                </Space>
                                            )}
                                        /> */}
                                        <Column
                                            title="Settled"
                                            key="Settled"
                                            render={(text, record) => (
                                                <Space size="middle">
                                                    {/* <button onClick={() => set_instance_status_Settled(item.instance_id) }></button> */}
                                                    <button onClick={() => changeExpand_Settled(index)}></button>
                                                </Space>
                                            )}
                                        />
                                    </Table>
                                </div>
                                {
                                    item.isExpand_ArbitrateUserWin ?
                                        <div className='strategy-form' >
                                            set_instance_status_ArbitrateUserWin  {item.instance_id}
                                            <br></br>
                                            <Form_ArbitrateUserWin set_instance_status_ArbitrateUserWin={set_instance_status_ArbitrateUserWin.bind(this)} />
                                        </div>
                                        : null
                                }
                                {
                                    item.isExpand_ArbitrateStrategistWin ?
                                        <div className='strategy-form' >
                                            set_instance_status_ArbitrateStrategistWin  {item.instance_id}
                                            <br></br>
                                            <Form_ArbitrateStrategistWin set_instance_status_ArbitrateStrategistWin={set_instance_status_ArbitrateStrategistWin.bind(this)} />
                                        </div>
                                        : null
                                }
                                {
                                    item.isExpand_Settled ?
                                        <div className='strategy-form' >
                                            set_instance_status_Settled  {item.instance_id}
                                            <br></br>
                                            <Form_Settled set_instance_status_Settled={set_instance_status_Settled.bind(this)} />
                                        </div>
                                        : null
                                }
                            </div>
                        </div>
                    )
                })
                }
                {connected && splitInstance && 
                    <div className="d-flex justify-content-center align-items-center ">
                        {/* <p>All Will Settled Instances</p> */}
                        <h3> </h3>
                    </div>
                }
            </div>
        </div>
    )
}
