
import statusTextMap from './statusTextMap';
import instanceStatusTextMap from './instanceStatusTextMap';

const getStatusText = (status) => {
    return statusTextMap[status] || '';
}
const getInstanceStatusText = (status) => {
    return instanceStatusTextMap[status] || '';
}
const getPassphraseDefaultText = (passphrase) => {
    return passphrase.toString() || 'your_passphrase';
}

//  allStrategies
const formatAllStrategies = (_allStrategies) => {
    
    _allStrategies.forEach(item => {
        item.strategy_id = item.strategy_id.toString();
        item.strategy_startup_amount = item.strategy_startup_amount.toString();
        item.stop_loss = item.stop_loss.toString();
        item.status = item.status.toString();
        item.tp_rate = item.tp_rate.toString();
        item.take_profit = item.take_profit.toString();
        item.id = item.name + Date.now();

        item.statusText = getStatusText(item.status);
        item.isExpand = false;
    });
    return _allStrategies

}

//  allInstances
const formatAllInstances = (_allInstances) => {
    
    _allInstances.forEach(item => {
        item.instance_id = item.instance_id.toString();
        item.strategy_id = item.strategy_id.toString();
        item.strategy_startup_amount = item.strategy_startup_amount.toString();
        item.stop_loss = item.stop_loss.toString();
        item.status = item.status.toString();
        item.tp_rate = item.tp_rate.toString();
        item.take_profit = item.take_profit.toString();
        item.id = item.name + Date.now();

        item.statusText = getStatusText(item.status);

        item.apikey = item.apikey.toString();
        item.secretkey = item.secretkey.toString();
        item.passphrase = getPassphraseDefaultText(item.passphrase);
        item.instance_status_text = getInstanceStatusText(item.instance_status);
        // item.isExpand = false;
        item.isExpand_StrategistPreConfirmed = false;
        item.isExpand_StrategistMidDisagree = false;
        item.isExpand_UserMidDisagree = false;
        item.isExpand_StrategistConfirmed = false;
        item.isExpand_ArbitrateUserWin = false;
        item.isExpand_ArbitrateStrategistWin = false;
        item.isExpand_Settled = false;
    });
    return _allInstances;

}
// export default formatAllStrategies;
export { formatAllStrategies, formatAllInstances };
