import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route, HashRouter } from "react-router-dom";

import Web3 from "web3";
import QuantJson from "./abis/Quant.json";

import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-icons/font/bootstrap-icons.css";

import Home from "./components/Home";
import Main from "./components/Main";

import "./css/main.css";
import Navbar from "./components/Navbar";
import Admin from "./components/Admin";
import Auditor from "./components/Auditor";
import Strategist from "./components/Strategist";
import User from "./components/User";
import Banner from "./components/Banner";
import Howtoplay from "./components/Howtoplay";
import Rule from "./components/Rule";
import Donation from "./components/Donation";
import Contact from "./components/Contact";

class App extends Component {

  async componentWillMount() {
    this.setState({ loading: false });
  }

  async ConnectAccount() {
    await this.loadWeb3();
  };

  async loadWeb3() {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.enable();
      this.loadBlockChatData();

    } else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider);
      this.loadBlockChatData();

    } else {
      window.alert(
        "Non-Ethereum browser detected. You should consider trying MetaMask!"
      );
    }
  }
 
  async loadBlockChatData() {
    const web3 = window.web3;
    const accounts = await web3.eth.getAccounts();
    web3.eth.defaultAccount = accounts[0];
    this.setState({ account: accounts[0] });

    const networkId = await web3.eth.net.getId();
    console.log("app.jsx loadBlockChatData networkId: ", networkId);

    // const networkData = LotteryJson.networks[networkId];
    
    // if (networkData) {
    if (networkId === 80001 || networkId === 137 || networkId == 1337) {
      this.setState({ connected: true });
      const CONTACT_ADDRESS = '0x5A41E5B354d411968ffB903E75DaF5d8Fe61EC7b';
      // const CONTACT_ADDRESS = '0x4202E7A3d5947A7A780a0ec2c1A29519342B5Cc2';
      console.log("CONTACT_ADDRESS");
      console.log(CONTACT_ADDRESS);
      // const quantContract = await web3.eth.Contract(LotteryJson.abi, networkAddress);
      const CONTACT_ABI = QuantJson.abi;
	    const quantContract = await web3.eth.Contract(CONTACT_ABI, CONTACT_ADDRESS);
      const networkAddress = CONTACT_ADDRESS;
      this.setState({ quantContract });
      this.setState({ networkAddress });
      this.setState({ networkId });
      if (networkId === 80001) {
          this.setState({ blockExplorerUrl: "https://mumbai.polygonscan.com/address/"+networkAddress });
      } else if (networkId === 137) {
          this.setState({ blockExplorerUrl: "https://polygonscan.com/address/"+networkAddress });
      } else if (networkId === 1337) {
        this.setState({ blockExplorerUrl: "https://polygonscan.com/address/"+networkAddress });
      } else {
        this.setState({ blockExplorerUrl: "https://polygonscan.com/address/"+networkAddress });
      }
      this.setState({ ticketPrice: "1" });

      this.setState({ loading: false });
    } else {
      window.alert("Blockchat contract not deployed to detected network.");
      this.switchToPolygonTestNet()
    }
  }

  
  async switchToPolygonTestNet(){
    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: "0x13881" }],
      });
      this.loadBlockChatData();
    } catch (error) {
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: "0x13881",
                chainName: "Polygon TestNet Mumbai",
                rpcUrls: ["https://rpc-mumbai.maticvigil.com/"],
                nativeCurrency: {
                  name: "Matic",
                  symbol: "Matic",
                  decimals: 18,
                },
                // blockExplorerUrls: ["https://explorer-mumbai.maticvigil.com"],
                blockExplorerUrls: ["https://mumbai.polygonscan.com"],
              },
            ],
          });
          this.loadBlockChatData();

        } catch (error) {
        }
      }
    }
  }


  async switchToPolygonMainnet() {
    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: "0x89" }],
      });
      this.loadBlockChatData();
    } catch (error) {
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: "0x89",
                chainName: "Polygon Mainnet",
                rpcUrls: ["https://rpc-mainnet.maticvigil.com/"],
                nativeCurrency: {
                  name: "Matic",
                  symbol: "Matic",
                  decimals: 18,
                },
                // blockExplorerUrls: ["https://explorer.matic.network/"],
                blockExplorerUrls: ["https://polygonscan.com/"],
              },
            ],
          });
          this.loadBlockChatData();

        } catch (error) {
        }
      }
    }
  }


  constructor(props) {
    super(props);
    this.state = {
      connected: false,
      account: "",
      gameStartDate: 0,
      quantContract: null,
      ticketPrice: 10**18,
      // ticketPrice: 0.1 * 10**18, // 0.1 matic
      loading: true,
    };
    // console.log(" this.state ", this.state)
    this.ConnectAccount = this.ConnectAccount.bind(this)
  }

  render() {
    return (!this.state.loading
      ?
      <HashRouter>
        <Navbar account={this.state.account} ConnectAccount={this.ConnectAccount} />
        <Switch>
          <Route path="/" exact>
            <Banner gameStartDate={this.state.gameStartDate} />
            <Home account={this.state.account} ticketPrice={this.state.ticketPrice} connected={this.state.connected} ConnectAccount={this.ConnectAccount} quantContract={this.state.quantContract} networkAddress={this.state.networkAddress} blockExplorerUrl={this.state.blockExplorerUrl}/>
          </Route >
          
          <Route path="/main" exact>
            <Banner gameStartDate={this.state.gameStartDate} />
            <Main account={this.state.account} ticketPrice={this.state.ticketPrice} connected={this.state.connected} ConnectAccount={this.ConnectAccount} quantContract={this.state.quantContract} networkAddress={this.state.networkAddress} blockExplorerUrl={this.state.blockExplorerUrl}/>
          </Route >

          <Route path="/admin" exact>
            <Admin account={this.state.account} ticketPrice={this.state.ticketPrice} connected={this.state.connected} ConnectAccount={this.ConnectAccount} quantContract={this.state.quantContract} networkAddress={this.state.networkAddress} blockExplorerUrl={this.state.blockExplorerUrl}/>
          </Route >

          <Route path="/strategist" exact>
            <Strategist account={this.state.account} ticketPrice={this.state.ticketPrice} connected={this.state.connected} ConnectAccount={this.ConnectAccount} quantContract={this.state.quantContract} networkAddress={this.state.networkAddress} blockExplorerUrl={this.state.blockExplorerUrl}/>
          </Route >

          <Route path="/user" exact>
            <User account={this.state.account} ticketPrice={this.state.ticketPrice} connected={this.state.connected} ConnectAccount={this.ConnectAccount} quantContract={this.state.quantContract} networkAddress={this.state.networkAddress} blockExplorerUrl={this.state.blockExplorerUrl}/>
          </Route >

          <Route path="/auditor" exact>
            <Auditor account={this.state.account} ticketPrice={this.state.ticketPrice} connected={this.state.connected} ConnectAccount={this.ConnectAccount} quantContract={this.state.quantContract} networkAddress={this.state.networkAddress} blockExplorerUrl={this.state.blockExplorerUrl}/>
          </Route >

          <Route path="/how-to-play" exact>
            <Howtoplay />
          </Route >

          {/* <Route path="/rule" exact>
            <Rule />
          </Route >

          <Route path="/donation" exact>
            <Donation />
          </Route >

          <Route path="/contact" exact>
            <Contact />
          </Route > */}

        </Switch >
      </HashRouter >
      :
      <div className="text-center">
        loading...
      </div>

    );
  }
}

export default App;
