import React, { useContext } from 'react'
import { IntlProvider } from 'react-intl' // react-reduxProvider react-intlapi
import { mainContext } from '../reducer' // useReducer  
import zh_CN from './cn' // 
import en_US from './en' // 

const Inter = (props) => {
    //   redux MobxreactuseReducer
  const { state } = useContext(mainContext)
  const chooseLocale = (val) => { 
    let _val = val || navigator.language.split('_')[0]
    switch (_val) {
      case 'en':
        return en_US
      case 'zh':
        return zh_CN
      default:
        return en_US
    }
  }
  let locale = state.locale || 'en' //  locale
  let { children } = props
  //  react-intlapi 
  return (
    <IntlProvider
      key={locale}
      locale={locale}
      defaultLocale='en'
      messages={chooseLocale(locale)}
    >
      {children}
    </IntlProvider>
  )
}

export default Inter
