import React, { useState } from 'react';
import { Form, Input, InputNumber, Button, Select } from 'antd';
import moment from "moment";
const { Option } = Select;
const layout = {
  labelCol: {
    span: 10,
  },
  wrapperCol: {
    span: 16,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};

class StrategyNewForm extends React.Component {
  formRef = React.createRef();
  onExchangeChange = (value) => {
    switch (value) {
		case 'binance':
		  this.formRef.current.setFieldsValue({
        strategy_name: 'bn' + moment().format('YYYYMMDDHHmmss'),
		  });
		  return;
		case 'okex':
		  this.formRef.current.setFieldsValue({
        strategy_name: 'ok' + moment().format('YYYYMMDDHHmmss'),
		  });
		  return;
		case 'huobi':
			this.formRef.current.setFieldsValue({
			  strategy_name: 'hb' + moment().format('YYYYMMDDHHmmss'),
			});
		case 'other':
			this.formRef.current.setFieldsValue({
				strategy_name: 'ot' + moment().format('YYYYMMDDHHmmss'),
			});
	  }
	};
  onFinish = (values) => {
    console.log(values);
	this.props.newStrategy(values);
  };
  onReset = () => {
    this.formRef.current.resetFields();
  };
  onFill = () => {
    this.formRef.current.setFieldsValue({
		strategy_name: 'bn' + moment().format('YYYYMMDDHHmmss'),
		startup_amount: 10000,
		take_profit: 2000,
		tp_rate: 30,
		stop_loss: 1000,
		sl_rate: 40,
		exchange: 'binance',
    });
  };

  render() {
    return (
      <Form {...layout} ref={this.formRef} name="control-ref" onFinish={this.onFinish}>
        <Form.Item
        name="strategy_name"
        label="Name"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Input />
      </Form.Item>
      <Form.Item
        name="startup_amount"
        label="Startup Amount"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <InputNumber />
      </Form.Item>
      <Form.Item
        name="take_profit"
        label="TakeProfit"
        rules={[
          {
            required: true,
          },
        ]}
      >
	  	<InputNumber />
      </Form.Item>
      <Form.Item
        name="tp_rate"
        label="TakeProfitRate"
        rules={[
          {
            required: true,
          },
        ]}
      >
	  	<InputNumber />
      </Form.Item>
      <Form.Item
        name="stop_loss"
        label="StopLoss"
        rules={[
          {
            required: true,
          },
        ]}
      >
	  	<InputNumber />
      </Form.Item>
      <Form.Item
        name="sl_rate"
        label="StopLossRate"
        rules={[
          {
            required: true,
          },
        ]}
      >
	  	<InputNumber />
      </Form.Item>
      <Form.Item
        name="exchange"
        label="Exchange"
        rules={[
          {
            required: true,
          },
        ]}
      >
        <Select
          placeholder="Select a option and change input text above"
          onChange={this.onExchangeChange}
          allowClear
        >
          <Option value="okex">okex</Option>
          <Option value="huobi">huobi</Option>
          <Option value="binance">binance</Option>
        </Select>
      </Form.Item>

      <Form.Item
        noStyle
        shouldUpdate={(prevValues, currentValues) => prevValues.exchange !== currentValues.exchange}
      >
        {({ getFieldValue }) =>
          getFieldValue('exchange') === 'okex' ? (
            <Form.Item
              name="passphrase"
              label="passphrase"
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Input />
            </Form.Item>
          ) : null
        }
      </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
          <Button htmlType="button" onClick={this.onReset}>
            Reset
          </Button>
          <Button type="link" htmlType="button" onClick={this.onFill}>
            Fill form
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

export default StrategyNewForm;